/* ----- Header ------------------------ */
.header {
  position: relative;
  .logo {
    margin-right: 15px;
    max-width: 100px; }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 11px 0;
    border-bottom: 3px solid rgba(255, 255, 255, 0.2); } }

.logo {
  a {
    display: block;
    outline: none; } }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  header {
    .container-full {
      padding: 0 35px; } }

  .header {
    .logo {
      max-width: none; } }

  /* end media min width 1000 pixels */ }


/* ----- END Header -------------------- */
