/* ----- Page structure ---------------- */
.clear {
  clear: both; }

.group:after {
  content: ".";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden; }

html, body {
  height: 100%; }

body {
  display: flex;
  flex-direction: column; }

#wrapper {
  min-width: 320px;
  position: relative;
  flex: 1 0 auto; }

.footer {
  flex-shrink: 0; }

.container, .container-full {
  padding-left: 15px;
  padding-right: 15px;
  width: auto; }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .bl-scroll {
    max-height: 100vh; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .container {
    max-width: 1170px;
    margin: 0 auto;
    &-full {
      max-width: none;
      width: 100%;
      margin: 0; } }
  /* end media min width 1200 pixels */ }























