/* ----- Forms ------------------------- */
.st-form {
  margin-bottom: 20px;

  &__tt {
    display: block; } }


.form-group {
  margin-bottom: 14px;

  &__label {
    display: block;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 18px; }

  &_inline {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .form-group__label {
      min-width: 140px;
      max-width: 140px;
      padding: 0 10px 0 0;
      margin: 5px 0; } } }

.form-group_check {
  display: flex;
  justify-content: flex-start;
  .form-group__label {
    order: 1; } }


.form-group_validate {
  input.error {
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
    border-bottom: none;
    &:hover, &:focus {
      border-bottom-color: transparent; }
    &:hover {
      + label {
        border-color: $input-hover-border-color; } }
    &:focus {
      + label {
        border-color: $input-focus-border-color; } } }
  label.error {
    padding-bottom: 5px;
    font-size: 10px;
    color: rgba(0, 49, 123, 0.8);
    line-height: 1;
    padding-left: 15px;
    background-color: #fff;
    display: block;
    border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
    text-align: left;
    color: red;
    border: $input-border;
    border-top: none; } }


