.form-light {
  box-shadow: 0 6px 15px #460a5b;
  border-radius: 24px 0 24px 24px;
  background-color: rgba(255, 255, 255, 0.6);
  background-color: #955095;
  padding: 25px; }

.header-dd-bl {
  position: relative;
  z-index: 1; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .form-group-grid {
    display: flex;
    justify-content: space-between;

    &__half-col {
      width: 48.5%; }

    &__three-col {
      width: 31.3%; }

    &__four-col {
      width: 22.8%; } }

  .form-group_validate {
    label.error {
      padding-left: 25px;
      padding-right: 25px; } }

