/* ----- Index page -------------------- */
.box-banner {
  background: rgba(255, 255, 255, 0.88);
  border-radius: 16px;
  padding-left: 10px;
  padding-right: 10px;
  max-width: 635px;
  margin: 0 auto; }

.box-banner__cap {
  padding: 12px 5px 7px;
  > * + * {
    margin-top: 10px; }
  .title-site_h2 {
    font-size: 30px;
    line-height: 37px;
    margin-bottom: 0; } }

.box-banner__body {
  background: #fff;
  padding: 14px 10px; }

.txt-tt {
  font-family: $site-font-family-2;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: #00317B;
  margin-bottom: 15px; }

.txt-tt-second {
  font-family: $site-font-family-2;
  font-weight: bold;
  margin-bottom: 15px;
  font-size: 15px;
  line-height: 24px;
  color: #2F78C1; }


.section-st {
  padding: 25px 0; }

.section-st_window-full {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 30px 0px;
  background: #fff url(../img/bg2-drop.jpg) center top repeat-y;
  background-size: cover;
  animation: {
    name: bg1;
    duration: 45s;
    timing-function: linear;
    iteration-count: infinite; }

  .container {
    width: 100%; }

  &_dark-bg {
    background-image: url(../img/bg3-drop.jpg);
    animation-name: bg2; } }

@keyframes bg1 {
  from {
    background-position: center 0; }
  to {
    background-position: center -2092px; } }

@keyframes bg2 {
  from {
    background-position: center 0; }
  to {
    background-position: center -2099px; } }

.group-btns {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 525px;
  margin: 0 auto;
  &__item {
    min-width: 50%;
    max-width: 50%;
    margin-bottom: 10px;
    padding-left: 5px;
    padding-right: 5px;
    .btn {
      font-size: 12px;
      padding-left: 5px;
      padding-right: 5px; }
    &.group-btns__item_max {
      min-width: 100%;
      max-width: 100%; }
    &.group-btns__item_three {
      min-width: 33.3%;
      max-width: 33.3%; }
    .btn_big.btn {
      font-size: 18px;
      padding: 9px 15px; }
    &:nth-child(2n) {
      padding-right: 0; }
    &:nth-child(2n-1) {
      padding-left: 0; } } }

.group-btn-soc {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 560px;
  margin: 0 auto;
  .btn {
    min-width: 49%;
    max-width: 49%; } }

.btn_soc {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 5px;
  padding-right: 5px;
  .btn__icon {
    flex-shrink: 0;
    order: 1;
    margin-left: 5px;
    margin-right: 0;
    max-width: 24px; }
  .btn__text-icon {
    font-weight: 500;
    font-family: $site-font-family-2;
    font-size: 12px; }
  .btn__text-icon {
    span {
      display: none; } } }

.box-banner__footer {
  padding: 9px 0 11px; }

.preview-form {
  text-align: center; }

.box_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0,0,0,0); }

//footer
.f-bl {
  padding: 10px 0;
  text-align: center;
  font-size: 10px;
  line-height: 14px;
  font-family: $site-font-family;
  font-weight: normal;
  color: #083F73;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  span {
    display: inline-block;
    padding: 0 4px; }
  a {
    color: #2F78C1;
    text-decoration: underline;
    &:hover, &:focus {
      text-decoration: none; } }
  ul {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
      &:last-child {
        &:after {
          display: none; } }
      &:after {
        content: "|";
        margin: 0 4px; } } } }

.cookies {
  background: rgba(216, 233, 246, 0.9);
  border-radius: 10px 0 0 10px;
  position: fixed;
  z-index: 2;
  bottom: 0;
  right: 0;
  max-width: 300px;
  padding: 18px 100px 18px 21px;
  font-family: $site-font-family;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  &_opened {
    display: none; }
  a {
    color: #2F78C1;
    &:hover {
      color: #2F78C1; } }
  p {
    margin-bottom: 0; } }

.cookies__close {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 25px;
  width: 25px;
  height: 25px;
  cursor: pointer;
  background: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18 2L2 18M2 2L18 18' stroke='%2300317B' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") center center no-repeat;
  &:hover {
    background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M18 2L2 18M2 2L18 18' stroke='%23000' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A"); } }

.txt-tt {
  .col-blu {
    display: block; } }

.br {
  display: block;
  margin-top: 7px; }

.col-blu {
  color: #2F78C1; }

.form-group-wrapp {
  max-width: 345px;
  margin: 0 auto; }

.form-group {
  .btn {
    padding: 8px 15px;
    font-size: 18px; } }

.line-download {
  background: #D8E9F7;
  border-radius: 5px;
  height: 7px;
  max-width: 460px;
  margin: 10px auto 0;
  span {
    height: 100%;
    background: linear-gradient(90.89deg, #00317B 0%, #426076 103.8%);
    border-radius: 5px;
    display: block;
    width: 0px; } }

.box-animation {
  .line-download {
    span {
      animation: line-d 3s linear forwards; } } }

@keyframes line-d {
  0% {
    width: 0%; }
  85% {
    width: 70%; }
  100% {
    width: 100%; } }

.txt-tt-second_sm.txt-tt-second {
  margin-bottom: 3px; }
.bl-value {
  font-family: $site-font-family-2;
  font-weight: 500;
  font-size: 12px;
  line-height: 17px;
  color: #00317B;
  margin-bottom: 5px;
  span {
    font-size: 14px;
    font-weight: bold; } }

.bl-value_lg {
  margin-bottom: 19px; }

.txt-tt-three {
  font-family: $site-font-family;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #00317B;
  margin-bottom: 10px; }

.danger-btn {
  background: #F05B51;
  border-radius: 21px;
  font-family: $site-font-family;
  font-weight: normal;
  line-height: 20px;
  font-size: 12px;
  line-height: 14px;
  padding: 8px 10px;
  color: #fff;
  max-width: 520px;
  margin: 0 auto 20px; }

.box-banner.box-banner_push-bottom {
  padding-bottom: 11px; }

.box-banner__body.box-banner__body_sm {
  padding-bottom: 1px; }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .line-download {
    margin-top: 20px; }
  .txt-tt_sm.txt-tt {
    margin-bottom: 15px; }
  .box-banner__body.box-banner__body_sm {
    padding-bottom: 15px; }
  .bl-value {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 10px;
    span {
      font-size: 18px; }
    &_lg {
      margin-bottom: 20px; } }
  .txt-tt-second.txt-tt-second_sm {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 24px; }
  .danger-btn {
    font-size: 18px;
    line-height: 21px;
    padding: 10px 15px; }
  .txt-tt-three {
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 12px; }
  .box-banner__body {
    padding: 22px 30px 10px; }
  .box-banner__footer {
    padding: 18px 0; }
  .box-banner__cap {
    padding: 24px 5px 18px; }
  .box-banner__cap .title-site_h2 {
    font-size: 42px;
    line-height: 51px; }
  .group-btn-soc .btn {
    min-width: calc(50% - 10px);
    max-width: calc(50% - 10px); }
  .btn_soc .btn__icon {
    order: 0;
    margin-left: 0;
    margin-right: 10px;
    max-width: none; }
  .btn_soc .btn__text-icon {
    font-size: 14px;
    letter-spacing: 0.01em;
    span {
      display: inline;
      font-weight: 600; } }
  .group-btns__item {
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 20px; }
  .group-btns__item .btn {
    font-size: 24px;
    padding: 18px 15px; }
  .group-btns {
    &__item {
      .btn_big.btn {
        font-size: 24px;
        padding: 18px 15px; } } }
  .txt-tt {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 25px;
    &-second {
      font-size: 24px;
      line-height: 29px;
      margin-bottom: 25px; } }
  .preview-img {
    margin-bottom: 30px; }
  .preview-img__inner {
    width: 258px;
    height: 366px; }
  .section-st {
    padding: 40px 0; }

  //footer
  .f-bl {
    padding: 6px 0;
    text-align: center;
    font-size: 12px;
    line-height: 14px; }

  .footer {
    position: fixed;
    z-index: 1;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, 0.88); }

  .txt-tt {
    .col-blu {
      display: inline; } }

  .form-group {
    .btn {
      padding: 13px 15px;
      font-size: 24px; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  footer .container-full {
    padding: 0 35px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  //footer
  .cookies {
    right: 0;
    bottom: 60px;
    font-size: 14px;
    line-height: 16px; }
  .cookies__close {
    right: 30px; }
  /* end media min width 1200 pixels */ }


/////////// MOBILE HEIGHT

/* max width 767 pixels */
@media only screen and (max-width: 767px) and (max-height: 750px) {
  .box_wrapp-height {
    .box-banner__cap {
      padding: 7px 5px 4px; }
    .txt-tt {
      font-size: 16px;
      line-height: 18px; }
    .txt-tt-second {
      font-size: 16px;
      line-height: 18px; }
    .box-banner__body {
      padding-bottom: 5px; }
    p {
      font-size: 12px;
      line-height: 14px; } }
  .cookies {
    font-size: 14px;
    line-height: 16px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 100px;
    &__close {
      right: 10px; } }
  /* end media max width 767 pixels */ }
/* max width 767 pixels */
@media only screen and (max-width: 767px) and (max-height: 600px) {
  .box_wrapp-height {
    .box-banner__cap .title-site_h2 {
      font-size: 26px;
      line-height: 1.2; }
    .box-banner__cap {
      padding: 7px 5px 4px; }
    .txt-tt {
      font-size: 14px;
      line-height: 16px; }
    .txt-tt-second {
      font-size: 14px;
      line-height: 16px; }
    .box-banner__body {
      padding-bottom: 5px; }
    .form-group {
      margin-bottom: 5px; }
    .bl-value_lg {
      margin-bottom: 10px; }
    p {
      margin-bottom: 15px; } }
  .cookies {
    font-size: 12px;
    line-height: 14px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-right: 50px;
    &__close {
      right: 10px; } }
  /* end media max width 767 pixels */ }

///////////DESKTOP Height
/* max width 767 pixels */
@media only screen and (min-width: 768px) and (max-height: 750px) {
  .box_wrapp-height {
    .box-banner__cap .title-site_h2 {
      font-size: 40px;
      line-height: 1.2; }
    .box-banner__cap {
      padding: 7px 5px 4px; }
    .txt-tt {
      font-size: 20px;
      line-height: 24px; }
    .txt-tt-second {
      font-size: 20px;
      line-height: 24px; }
    .box-banner__body {
      padding-bottom: 5px; }
    .form-group {
      margin-bottom: 10px; }
    p {
      font-size: 16px;
      line-height: 18px; }
    .box-banner__footer {
      padding-top: 5px;
      padding-bottom: 10px; } }
  .cookies {
    font-size: 14px;
    line-height: 16px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 100px;
    &__close {
      right: 10px; } }
  /* end media max width 767 pixels */ }

/* ----- END index page /--------------- */
