/* ----- Card ------------------- */
.main-card-section {
  background: #F2E7F6;
  padding: 30px 0 0; }

.main-cards-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  padding: 50px 0 80px;
  &__item {
    min-width: 100%;
    max-width: 100%;
    padding-left: 5px;
    padding-right: 5px; } }

.card {
  text-align: center;
  margin-bottom: 20px;
  font-family: $site-font-family-3;
  font-weight: normal;
  &__icon {
    min-height: 71px;
    line-height: 71px;
    text-align: center;
    margin-bottom: 15px; }
  &__info {
    > *+* {
      margin-top: 7px; } }
  &__tt {
    color: #00317B;
    font-size: 16px;
    line-height: 20px;
    font-weight: 600; }
  &__txt {
    color: rgba(79, 79, 79, 0.8);
    font-size: 12px;
    line-height: 14px; } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .main-cards-grid {
    margin-left: -10px;
    margin-right: -10px;
    padding: 30px 0;
    &__item {
      padding-left: 10px;
      padding-right: 10px;
      min-width: 50%;
      max-width: 50%; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-cards-grid {
    padding: 70px 0 30px;
    &__item {
      min-width: 33.3%;
      max-width: 33.3%; } }

  .card {
    max-width: 290px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px; }

  .card__txt {
    font-size: 13px;
    line-height: 18px; }

  .card__tt {
    font-size: 18px;
    line-height: 22px; }
  .main-card-section {
    padding: 0 0; }
  /* end media min width 1000 pixels */ }

/* ----- END Card --------------- */
