/* ----- Controls styles ----------------- */
.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: $btn-padding;
  font: $btn-font;
  border: none;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  color: #fff;
  background-color: transparent;
  background-image: linear-gradient(90.89deg, #00317B 0%, #00255C 103.8%);

  &:hover {
    text-decoration: none;
    color: #fff;
    background-color: #00F09A;
    background-image: linear-gradient(0deg, #00317B 0%, #00255C 103.8%); }

  &_light-st {
    border-color: #fff;
    background-color: transparent;
    color: #fff;
    &:hover {
      background-color: #fff;
      color: #000; } }

  &_danger {
    border-color: #E70000;
    color: #E70000;
    &:hover {
      background-color: #E70000;
      color: #fff; } }

  &_border {
    line-height: 20px;
    border: 2px solid rgba(255, 255, 255, 0.9);
    background: transparent;
    &:hover, &:focus {
      background-image: none; } }


  &_second-st {
    background: #D8E9F7;
    border-radius: 5px;
    border: none;
    color: #426076;
    &:hover {
      background: darken(#D8E9F7, 10%);
      color: #426076; } }

  &_min-width {
    min-width: $btn-width-min; }

  &_max {
    width: 100%; }

  &_sm-txt {
    font-size: 14px;
    font-weight: 600;

    &.btn_min-width {
      min-width: $btn-width-min-sm; } }

  &_sm {
    padding: $btn-padding-sm;
    font-size: 14px;
    font-weight: 600;

    &.btn_min-width {
      min-width: $btn-width-min-sm; } }

  &_lg {
    padding: 11px 25px;
    font-size: 24px;
    line-height: 24px;


    &.btn_min-width {
      min-width: 198px; } }

  &_bold {
    font-weight: bold; }

  &_up {
    text-transform: uppercase; } }

input.btn {
  padding-top: 0;
  padding-bottom: 0;
  height: $input-height;
  width: auto;
  &_max {
    width: 100%; } }

.form-group-grid__four-col {
  .btn {
    vertical-align: bottom;
    display: block; } }

.placeholder {
  color: $st-color-placeholder; }

input,
textarea {
  @include placeholder-color($st-color-placeholder); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input,
textarea {
  position: relative;
  height: 40px;
  width: 100%;
  padding: $input-padding;
  border: $input-border;
  font: $input-font;
  color: $input-color;
  background-color: $input-fill-color;
  outline: none;
  border-radius: $input-radius;
  -webkit-appearance: none;
  box-shadow: $input-shadow;

  &:hover {
    border-color: $input-hover-border-color; }

  &:focus {
    border-color: $input-focus-border-color; } }

input[type="radio"], input[type="checkbox"] {
  width: auto;
  margin: 3px 5px 3px 0;
  height: 12px; }

input[type="checkbox"] {
  -webkit-appearance: checkbox; }
input[type="radio"] {
  -webkit-appearance: radio; }

textarea {
  vertical-align: top;
  height: $textarea-height;
  padding: $textarea-padding;
  resize: none; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {

  .preview-form {
    input.form-group__field {
      height: 50px;
      font-size: 16px;
      padding-left: 25px;
      padding-right: 25px; } }

  /* end media min width 768 pixels */ }

/* ----- END controls ------------------- */
