/* ----- Stepone page ------------------- */

.section-banner {
  min-height: 100vh;
  padding: 130px 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #E5F0FF url(../img/bg-step1-1.png) center center no-repeat;
  background-size: auto 100%;
  &_second-bg {
    background-image: url(../img/bg-step2-1.png); }
  .container {
    width: 100%; }
  &__inner {
    text-align: center;
    .title-site.title-site_h3 {
      font-size: 30px;
      line-height: 37px;
      margin-bottom: 20px; }
    p {
      font-family: $site-font-family;
      font-size: 12px;
      line-height: 18px; } } }

.txt-tt_small {
  margin-bottom: 7px; }

.section-banner__inner {
  .box-banner__cap {
    padding-top: 0; }
  .box-banner {
    background: none;
    &__body {
      background: none; } }
  .box-banner__footer {
    padding-top: 0;
    padding-bottom: 0; }

  .btn_soc {
    background-color: #fff;
    box-shadow: 5px 5px 10px rgba(#000, 0.5);
    &:hover, &:focus {
      background-image: linear-gradient(90.89deg, #00317B 0%, #00255C 103.8%);
      color: #fff;
      box-shadow: 0px 0px 5px rgba(#000, 0.5); } }
  .group-btn-soc {
    max-width: 540px; } }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .section-banner__inner {
    max-width: 660px;
    margin: 0 auto;
    .title-site.title-site_h2, h2, .title-site.title-site_h3, h3 {
      font-size: 37px;
      line-height: 45px; }
    p {
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 20px;
      .txt-lg {
        font-size: 18px;
        line-height: 20px; } }
    .txt-tt_small {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 10px; }
    .group-btns {
      max-width: 460px; } }
  .section-banner {
    background-image: url(../img/bg-step1-desc.png);
    padding: 140px 0 160px;
    background-size: auto 100%;
    &_second-bg {
      background-image: url(../img/bg-step2-desc.png); } }

  .cookies.cookies_left {
    bottom: 0;
    right: auto;
    left: 0;
    background: rgba(255, 255, 255, 0.88);
    border-radius: 0px 27.5743px 27.5743px 0px;
    max-width: 350px;
    padding-right: 70px;
    .cookies__close {
      right: 20px; } }

  .footer.footer_right {
    text-align: left;
    width: 510px;
    left: auto;
    bottom: 0;
    right: 0;
    border-radius: 20px 0 0 20px;
    padding: 2px 0;
    .f-bl {
      justify-content: flex-start; } }
  /* end media min width 768 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) and (max-height: 1000px) {
  .section-banner {
    background-size: auto 100%; }
  .section-banner__inner {
    max-width: 600px; }
  /* end media min width 768 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) and (max-height: 860px) {
  .section-banner {
    background-size: auto 110%; }
  .section-banner__inner {
    max-width: 600px;
    .box_wrapp-height_big {
      padding: 20px 0 40px; } }
  /* end media min width 768 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) and (max-height: 750px) {
  .section-banner__inner {
    max-width: 500px;
    .title-site.title-site_h2, h2, .title-site.title-site_h3, h3 {
      font-size: 34px;
      line-height: 40px;
      margin-bottom: 10px; } }
  .section-banner {
    background-size: auto 140%;
    padding: 65px 0; }
  .section-banner__inner {
    .box_wrapp-height_big {
      padding: 0; } }
  /* end media min width 768 pixels */ }

////MOBILE

/* Min width 768 pixels */
@media only screen and (max-width: 767px) and (max-height: 650px) {
  .section-banner {
    background-size: auto 110%;
    padding: 85px 0 30px; }
  .section-banner__inner {
    .danger-btn {
      margin-bottom: 5px; } }
  .section-banner__inner p {
    line-height: 14px; }
  /* end media min width 768 pixels */ }

/* ----- END Stepone page --------------- */
